.skillsPage{
    margin: 20px 10vw;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, 15rem);
    grid-gap: 15px;
    
    .skill{
        padding: 10px;
        overflow: hidden;
        color: #B8B8B8;
        
        h5{
            margin-top: 5px;
            font-family: Consolas, sans-serif;
            font-size: large;
            }
        }
        
    }