*{
    margin: 0;
    padding: 0;
    }

div{
    margin: 0;
    padding: 0;
    }

h1{
    font-size: 3.8rem;
    color: #FFBE1F;
    }

.infoScreen{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    
    .info{
        width: 65vw;
        margin-top: 35vh;
        padding: 0 30px 20px;
        font-family: 'Consolas', sans-serif;
        font-size: x-large;
        position: static;
        text-align: justify;
        color: white;
        
        hr{
            margin: 10px 0;
            border: 1px solid white;
            }
        }
    
    .nameContainer{
        display: flex;
        align-items: center;
        flex-direction: row;
        text-align: left;
        }
    
    .infoName{
        margin: 0 20px;
        overflow: hidden;
        }
    
    img{
        width: 15%;
        border: 3px solid white;
        border-radius: 50%;
        justify-self: center;
        
        }
    
    .curriculumLink{
        width: fit-content;
        margin: 20px auto;
        padding: 10px;
        background: #FFBE1F;
        border-radius: 5px;
        text-align: center;
        
        a{
            text-decoration: none;
            color: black;
            
            }
        }
    }

@media (max-width: 768px){
    .infoScreen{
        
        .info{
            width: 90vw;
            }
        
        img{
            width: 25%;
            }
        }
    }

@media (max-width: 425px){
    h1{
        font-size: 3rem;
        }
    .infoScreen{
        font-size: medium;
        
        .info{
            width: 80vw;
            margin: 5vh 0 0;
            padding: 0;
            font-size: medium;
            position: revert;
            bottom: revert;
            }
        
        .nameContainer{
            flex-direction: column;
            text-align: center;
            }
        
        img{
            width: 45vw;
            margin: 20px;
            }
        }
    }

