*{
    margin: 0;
    padding: 0;
    }

.menu{
    padding: 0 30px;
    background: #051E42; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #0F0C29, #051E42, #0F0C29); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0F0C29, #051E42, #0F0C29);
    display: flex;
    list-style: none;
    }

.containerMenu{
    margin: 5px;
    padding: 10px 0;
    }

.menuItem{
    padding: 5px;
    font-family: 'BankGothic Lt BT', serif;
    font-size: large;
    text-decoration: none;
    color: #FFBE1F;
    
    &:hover{
        border-bottom: 1px solid #FFBE1F;
        transition: all 1s;
        }
    }

.active{
    background: #FFFFFF;
    border-radius: 5px;
    font-family: 'BankGothic Lt BT', serif;
    text-decoration: none;
    color: #000000;
    
    }

@media (max-width: 425px){
    .menu{
        padding: 5px;
        justify-content: center;
        }
    .containerMenu{
        margin: 1px 2px;
        }
    .menuItem{
        font-size: medium;
        }
    }




