h2{
    font-size: 2.5rem;
    color: #FFBE1F;
    }

.projectsPage{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-items: center;
    }


.projectContainer{
    width: 55vw;
    margin: 20px;
    
    padding: 20px;
    
    font-family: Consolas, sans-serif;
    font-size: large;
    
    display: flex;
    flex-direction: column;
    
    text-align: justify;
    color: white;
    
    
    img{
        height: 80%;
        border-radius: 20px;
        }
    
    .descriptionContainer{
        margin: 10px;
        display: flex;
        align-items: center;
        flex-direction: column;
        
        
        .projectTitle{
            margin-bottom: 10px;
            text-align: center;
            }
        
        .description{
            padding: 0 30px;
            text-align: center;
            }
        
        .techContainer{
            display: none;
            justify-content: center;
            list-style: none;
            grid-template-columns: repeat(auto-fit, 6rem);
            grid-gap: 5px;
            
            .category{
                width: 100%;
                margin: 0 0 5px 0;
                padding: 5px 0;
                background: #FFBE1F;
                border-radius: 5px;
                text-align: center;
                color: black;
                }
            
            .tech{
                width: 100%;
                margin: 0 0 5px 0;
                padding: 5px 0;
                border-radius: 5px;
                text-align: center;
                color: #FFBE1F;
                
                }
                
            }
            
        }
        
    }


@media (max-width: 769px){
    
    .projectContainer{
        width: 80vw;
        
        img{
            width: 25vw;
            height: 25vw;
            }
        
        .descriptionContainer{
            align-items: start;
            flex-direction: column;
            
            .description{
                margin-left: 0;
                }
            
            .techContainer{
                margin-top: 10px;
                grid-template-columns: repeat(auto-fill, 8rem);
                }
                
            }
            
            
        }
        
    }

@media (max-width: 425px){
    .projectContainer{
        width: 80vw;
        align-items: center;
        flex-direction: column;
        
        img{
            width: 70vw;
            height: 70vw;
            margin: 10px 0;
            }
        
        .descriptionContainer{
            margin: 0; align-items: center;
            
            .description{
                margin-left: 0;
                font-size: medium;
                
                .projectTitle{
                    text-align: center;
                    }
                }
            }
            
        }
    }
